/*@import "@fortawesome/fontawesome-free/css/all.css"; */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  position: relative;
  min-height: 100%;
}
body {
  margin-bottom: 200px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100%;
  color: #333;
}

main {
  overflow: auto;
}

section {
  overflow: auto;
  /*padding-top: 10px;*/
  padding-bottom: 10px;
}

.floatLeft {
  float: left;
  margin-top: 20px;
}  

.floatLeft img {
  display: block;
  margin: auto;
  width: 40%;
  height: 40%;
}

.container {
  margin-top: 2em;
  /*text-align: center;*/
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  width: 100%;
}

span p {
  margin-top: 2em;
}

.logo {
  width: 100px;
  height: 100px;
}

h1 {
  margin-top: 10px;
  margin-bottom: 2em;
  font-size: 2.5rem;
}

h2 {
  margin-top: 1em;
  margin-bottom: 1em;
}

.container h3 {
  text-align: justify;
}

/* Navegacion principal index.html */

.main_nav {
  text-align: center;
}

nav ul {
  list-style: none;
  background-color: #5abea4;
  line-height: 2em;
  
}

nav ul li {
  display: inline-block;
  padding-top: 15px;
  padding-left: 15px;
  padding-right: 15px;
  font-size: 1.5rem;
  font-weight: 700;
}

nav ul li:first-child {
  padding-left: 0;
}

nav ul li:last-child {
  padding-right: 0;
}

nav ul li a {
  border-bottom: none;
}

nav ul li a:hover {
  color: #fff;
}

nav ul li.li_right {
  border-left: 4px solid #fff;
}

a {
  color: #333;
  text-decoration: none;
}

/* Lista ordenada */

ol li {
  margin-top: 10px;
  padding-top: 5px;
  padding-left: 5px;
  padding-right: 8px;
  text-align: justify;
}

ol li:first-child {
  padding-left: 0;
}

ol li:last-child {
  padding-right: 0;
}

abbr {
  text-decoration: none;
}

.mb, .fe {
  background-color: #f0f0f0;
}

.mb h4, .fe h4 {
  text-align: center;
  background-color: #3eb991;
}

.fe h4 {
  text-align: center;
  background-color: #e9a820;
}

.mb p, .fe p {
  padding: 10px;
  text-align: justify;
}

/* Navegaci칩n Breadcrumb (Miga de pan) */

.breadcrumb {
  background-color: #AFDDCE;
  border-radius: 0rem !important;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  padding: 0.75rem 1rem;
  margin-bottom: 1rem;
  list-style: none;
}

.breadcrumb a:hover {
  color: #22272D;
}

.breadcrumb-item + .breadcrumb-item {
  padding-left: 0.5rem;
}

.breadcrumb-item + .breadcrumb-item::before {
  display: inline-block;
  padding-right: 0.5rem;
  color: #6c757d;
  content: "/";
}

.breadcrumb-item + .breadcrumb-item:hover::before {
  text-decoration: underline;
}

.breadcrumb-item + .breadcrumb-item:hover::before {
  text-decoration: none;
}

.breadcrumb-item.active {
  color: #6c757d;
}

.others h4 {
  background-color: #af3eb9;
  line-height: 25px;
  text-align: left;
  width: 50%;
  margin-bottom: 5px;
  padding-left: 5px;
}

/* Lista no ordenada */
ul {
  list-style: none;
}

ul li {
  padding-top: 5px;
  text-align: justify;
}

ul li:first-child {
  padding-left: 15px;
}

ul li:last-child {
  padding-right: 0;
}


/* =======================================================
   Adaptacion de los contenidos de la clase .container
a los diferentes tama침os de dispositivos
========================================================== */

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
  
  .floatLeft {
    float: left;
    margin-top: 20px;
  } 

  .floatRight {
    float: right;
    margin-top: 20px;
    padding-left: 20px;
  }

  .mb div.floatLeft {
    width: 50%;
    padding: 15px;
  }
 
 .fe_lang div.floatLeft{
    width: 60%;
    padding: 15px;
  }

  .fe div.floatLeft {
    width: 33%;
    padding: 15px;
  }


  .btn-breadcrumb { 
    margin-left: 10px; 
    margin-right: 10px; 
    width: calc(100% - 20px) !important; 
}
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}

/* ====== footer ======== */

footer {
  background-color: #333;
  color: #6c757d;
  /*height: 18%;*/
  font-size: 0.8em;
  position: absolute;
  bottom: 0;
  width: 100%;
  /*padding-top: 15px;*/
  padding-bottom: 10px;
  text-align: center;
}

footer div.container img {
  width: 5%;
  height: 5%;
}

footer span p {
  font-weight: bold;
  margin-top: 5px;
  margin-bottom: 15px;
}

/* Transici칩n clip-path */
footer img{
  clip-path: polygon(50% 0%, 100% 0%, 100% 40%, 100% 55%, 100% 100%, 50% 100%, 0% 100%, 0% 55%, 0% 40%, 0% 0%);
  transition: all 2s;
}

/* Evento hover: se crea una estrella al pasar el rat칩n por el logotipo con clip-path */
footer img:hover{
  clip-path: polygon(50% 5%, 57% 40%, 80% 40%, 60% 55%, 70% 90%, 50% 70%, 30% 90%, 40% 55%, 20% 40%, 43% 40%);
  background-color: #088E66;

}

/* Animacion de los titulos h2 */

h2{
  width: 100%;
  float: left;
  -webkit-clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 80%);
  clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 80%);
  transform: translateY(-50px);
  opacity: 0;
  animation-name: titleAnimation;
  animation-timing-function:ease;
  animation-duration: 3s;

 -webkit-animation-fill-mode: forwards;
}

@keyframes titleAnimation {
  0% {
    transform: translateY(-50px);
    opacity: 0;
    -webkit-clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 80%);
    clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 80%);
  }
  20% {
    transform: translateY(0);
    opacity: 1;
    -webkit-clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 15%);
    clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 15%);
  }
  40% {
     transform: translateY(0);
     opacity: 1;
    -webkit-clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 15%);
    clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 15%);
  }
  60% {
     transform: translateY(50px);
     opacity: 0;
    -webkit-clip-path: polygon(100% 0, 100% -0%, 0 100%, 0 100%);
    clip-path: polygon(100% 0, 100% -0%, 0 100%, 0 100%);
  
  }
  80% {
    transform: translateY(50px);
    opacity: 0;
    -webkit-clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 80%);
    clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 80%);
  }
  100% {
    transform: translateY(0);
    opacity: 1;
    -webkit-clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 15%);
    clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 15%);
  }
  
}

.floatLeft video {
  width: 100%;
}

.floatLeft_wp picture {
  width: 30%;
}

.floatLeft img, .floatRight img {
  max-width: 100%;
  height: auto;
}